.about {
  width: 100%;
  height: auto;
  padding-top: 125px;
  // padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff5f6;

  background-image: url(../images/about/bg1mobile-320.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0px 790px;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .about {
      background-image: url(../images/about/bg2mobile@2x.png);
    }
  }

  @media screen and (max-width: 767px) {
    .background-container {
      background-image: url(../images/about/cow-280.png);
      background-size: 280px;
      background-repeat: no-repeat;
      background-position: 20px 133px;
      // background-position: center;

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/about/cow-280@2x.png);
      }
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-top: 130px;
    padding-bottom: 65px;
  }

  @media screen and (min-width: 768px) {
    .background-container {
      background-image: url(../images/about/1tablet-cow-340.png);
      background-size: 340px;
      background-repeat: no-repeat;
      background-position: 35px 140px;

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/about/1tablet-cow2@2x-680.png);
      }
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url(../images/about/bg1desktop.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0px 342px;

    .background-container {
      background-image: url(../images/about/home-pic1-1desktop.png);
      background-size: 516px;
      background-repeat: no-repeat;
      background-position: 40px 165px;

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/about/bg2desktop@2x.png),
          url(../images/about/home-pic2-1desktop@2x.png);
      }
    }
  }
}

.about-label {
  @include font-shs(18px, 1.17, 0.04em);
  font-family: Titan One;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin-bottom: 15px;

  @media screen and (min-width: 768px) {
    @include font-shs(26px, 1.15, 0.04em);
  }

  @media screen and (min-width: 1280px) {
    @include font-shs(30px, 1.13, 0.04em);
  }
}

.about-container {
  max-width: 220px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: left;

  @media screen and (min-width: 768px) {
    max-width: 340px;
    margin-left: auto;
    margin-right: 0;
  }

  @media screen and (min-width: 1280px) {
    max-width: 463px;
    height: 510px;
  }
}

.about-title {
  @include font-shs(34px, 1.15, 0.04em);
  font-family: Titan One;
  text-align: center;
  text-transform: uppercase;

  // заменить цвета на переменные
  color: #d41443;
  margin-bottom: 351px;

  @media screen and (min-width: 768px) {
    @include font-shs(48px, 1.14, 0.04em);
    margin-bottom: 100px;
  }

  @media screen and (min-width: 1280px) {
    @include font-shs(58px, 1.14, 0.06em);
    margin-bottom: 106px;
  }
}

.about-article {
  @include font(500, 12px, 1.6, 0.04em);
  color: #907e82;

  @media screen and (min-width: 1280px) {
    @include font(500, 14px, 1.9, 0.04em);
  }
}

.accent-text {
  @include font(500, 12px, 1.6, 0.04em);
  color: #000000;

  @media screen and (min-width: 768px) {
    @include font(500, 14px, 1.8, 0.04em);
  }

  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.7, 0.04em);
  }
}

.about-text {
  &:nth-child(1) {
    font-weight: bold;
  }
  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
    &:nth-child(1) {
      margin-bottom: 45px;
    }
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    &:nth-child(1) {
      margin-bottom: 49px;
    }

    &:last-child {
      margin-bottom: 62px;
    }
  }

  @media screen and (min-width: 1280px) {
    &:not(:last-child) {
      margin-bottom: 35px;
    }

    &:last-child {
      margin-bottom: 45px;
    }
  }
}

.about-button {
  max-width: 175px;
  width: 100%;
}
